<template>
  <div class="btn-wrapper">
    <button @click="login" type="primary" block>授权登录</button>
  </div>
</template>
<script>
import axios from 'axios'
import { apiHost } from '@/utils/config'

axios.defaults.withCredentials = true

export default {
  components: {
  },
  methods: {
    login () {
      axios.get(`${apiHost}/dinner/a/wechat/api/user/getUserByCode`, {
        params: {
          uid: 'admin'
        }
      })
        .then((response) => {
          const { loginName, openId } = response.data.body
          localStorage.setItem('auth', loginName)
          localStorage.setItem('openId', openId)
          this.$router.push(this.$route.query.redirect || '/')
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>
